.shared-host-password-features{
    clip-path: polygon(0 0, 100% 15%, 100% 100%, 61% 100%, 0 100%);
    background: linear-gradient(rgba(0,0,0,0.75), rgba(0,0,0,0.75)), url('../../../assets/Home/hostpassword-features.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    padding:10% 10% 5%;
    margin:5% 0;
    color:#fff;
    h1{
        font-size: 35px;
        text-align: center;
        margin-bottom: 50px;
    }
    .row1{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
    }
    .item-col{
        width: 25%;
    }
    .features-item{
        margin:10px 0;
        width: 100%;
        padding:25px;
        border-radius: 10px;
        &:hover{
            background: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5));
            transform: scale(1.035);
            transition: 0.35s ease-in-out;
        }
    }
    .img{
        content: url('../../../assets/Home/hostpassword-features2.png');
        width: 30%;
    }
    h6{
        font-size: 18px;
        margin:15px 0;
    }
    p{
        font-size: 18px;
    }
    .icon{
        background: linear-gradient(rgba(255,255,255,0.15), rgba(255,255,255,0.15));
        display: flex;
        align-items: center;
        justify-content: center;
        width: 90px;
        height: 90px;
        border-radius: 50%;
        font-size: 45px;
    }
}

@media(max-width:1401px){
    .shared-host-password-features{
        clip-path: polygon(0 0, 100% 10%, 100% 100%, 61% 100%, 0 100%);
        padding:10% 10% 5%;
        margin:5% 0;
        h1{
            font-size: 30px;
            margin-bottom: 40px;
        }
        .item-col{
            width: 27%;
        }
        .features-item{
            margin:10px 0;
            padding:17.5px;
        }
        .img{
            width: 30%;
        }
        h6{
            font-size: 15px;
            margin:15px 0;
        }
        p{
            font-size: 15px;
        }
        .icon{
            width: 70px;
            height: 70px;
            font-size: 40px;
        }
    }
}

@media(max-width:1401px){
    .shared-host-password-features{
        clip-path: polygon(0 0, 100% 8%, 100% 100%, 61% 100%, 0 100%);
        padding:12% 7.5% 5%;
        .item-col{
            width: 30%;
        }
        .img{
            width: 30%;
        }
    }
}

@media(max-width:767px){
    .shared-host-password-features{
        clip-path: polygon(0 0, 100% 5%, 100% 100%, 61% 100%, 0 100%);
        padding:15% 5% 10%;
        margin:40px 0;
        h1{
            font-size: 20px;
            margin-bottom: 20px;
        }
        .item-col{
            width: 100%;
            display: flex;
            justify-content: space-between;
        }
        .features-item{
            margin:10px 0;
            width: 47.5%;
            padding:15px;        
        }
        .img{
            width: 280px;
            max-width: 100%;
            margin:10px auto;
        }
        h6{
            font-size: 14px;
            margin:10px 0;
        }
        p{
            font-size: 14px;
        }
        .icon{
            width: 60px;
            height: 60px;
            font-size: 30px;
        }
    }
}

@media(max-width:500px){
    .shared-host-password-features{
        padding:25% 7.5% 10%;
        .item-col{
            flex-wrap: wrap;
        }
        .features-item{
            width: 100%;
        }
    }
}
@media(max-width:450px){
    .shared-host-password-features{
        clip-path: polygon(0 0, 100% 5%, 100% 100%, 61% 100%, 0 100%);
        margin:40px 0;
    }
}