.shared-pricing-plan{
    margin:5% 0;
    .h5{
        font-size: 19px;
        color:#427CFF;
        text-align: center;
        padding:0 7.5%;
    }
    h1{
        font-size: 35px;
        text-align: center;
        padding:0 7.5%;
        margin:15px 0 30px;
    }
    .react-tabs__tab-list{
        width: fit-content;
        border:1px solid #ddd;
        display: flex;
        border-radius: 5px;
        margin:0 auto 100px;
    }
    .react-tabs__tab{
        text-transform: uppercase;
        padding:10px 45px;
        margin:3px;
        border:1.5px solid transparent;
        font-size: 18px;
        color:#333;
        background-color: #fff;
        cursor: pointer;
    }
    .react-tabs__tab--selected{
        border:1.5px solid #427CFF;
        color:#427CFF;
        border-radius: 5px;
        background-color: #f0f4ff;
    }
}

.shared-pricing-plan .tab-panel1{
    padding:0 14%;
    position: relative;
    .bg-img{
        content:url('../../../assets/Home/cards-bg.png');
        background: linear-gradient(rgba(58, 87, 167, 0.5), rgba(58, 87, 167,0.5));
        filter: brightness(0.8);
        position: absolute;
        width: 100%;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        clip-path: polygon(0 40%, 100% 55%, 100% 75%, 0 60%);

    }
    .react-tabs__tab-panel{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        position: relative;
    }
}

.shared-pricing-plan .single-card{
    background-color: #fff;
    width: 32%;
    border:1px solid #dfdfdf;
    padding:25px;
    border-radius: 15px;
    position: relative;
    .logo-container{
        width: fit-content;
        padding:10px;
        position: relative;
        margin:-45px auto 0;
        span{
            color:white;
            position: relative;
            font-size: 17px;
        }
    }
    .logo{
        content: url('../../../assets/Home/logo.png');
        width: 100%;
        position: absolute;
        left:50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    h5{
        font-size: 32.5px;
        text-align: center;
        margin:30px 0 20px;
    }
    h6{
        font-size: 55px;
        text-align: center;
        color:#427CFF;
        font-weight: normal;
        margin-bottom: 10px;
        sup{
            font-size: 30px;
        }
    }
    .price{
        text-align: center;
        margin-bottom: 40px;
    }
    .price span{
        font-size: 17px;
        color:#555;
        width: fit-content;
        margin:0 auto;
    }
    .p{
        font-size: 17px;
        text-align: center;
        margin-bottom: 25px;
    }
    .feature-item{
        display: flex;
        align-items: center;
        font-size: 18px;
        margin:15px 0;
        svg{
            font-size: 22px;
            color:#427CFF;
            margin-right: 15px;
        }
        .inner-p{
            flex: 1;
        }
    }
    .features{
        margin-bottom: 120px;
    }
    .last-row{
        position: absolute;
        left:25px;
        right: 25px;
        bottom:20px;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 20px;
        a{
            background-color: #427CFF;
            color:white;
            font-size: 18px;
            padding:15px 40px;
            border-radius: 30px;
        }
        .card{
            font-size: 18px;
            color:#555;
            padding-top: 10px;
        }
    }
}

@media(max-width:1401px){
    .shared-pricing-plan{
        .h5{
            font-size: 16px;
        }
        h1{
            font-size: 30px;
            margin:15px 0 30px;
        }
        .react-tabs__tab-list{
            margin:0 auto 90px;
        }
        .react-tabs__tab{
            padding:10px 40px;
            font-size: 15px;
            margin:2px;
        }
    }
    
    .shared-pricing-plan .tab-panel1{
        padding:0 10%;
        .bg-img{
            clip-path: polygon(0 40%, 100% 55%, 100% 75%, 0 60%);
    
        }
    }
    
    .shared-pricing-plan .single-card{
        padding:20px;
        .logo-container{
            margin:-35px auto 0;
            span{
                font-size: 14px;
            }
        }
        h5{
            font-size: 27px;
            margin:25px 0 15px;
        }
        h6{
            font-size: 42.5px;
            sup{
                font-size: 25px;
            }
        }
        .price{
            margin-bottom: 35px;
        }
        .price span{
            font-size: 15px;
        }
        .p{
            font-size: 14px;
            margin-bottom: 20px;
        }
        .feature-item{
            font-size: 15px;
            margin:12.5px 0;
            svg{
                font-size: 20px;
                margin-right: 12.5px;
            }
        }
        .features{
            margin-bottom: 100px;
        }
        .last-row{   
            left:20px;
            right: 20px;
            bottom:15px;
            padding-top: 15px;
            a{
                font-size: 15px;
                padding:13px 35px;
                border-radius: 25px;
            }
            .card{
                font-size: 15px;
                padding-top: 10px;
            }
        }
    }
    
}

@media(max-width:1025px){
    .shared-pricing-plan .tab-panel1{
        padding:0 5%;
    }
}

@media(max-width:850px){
    .shared-pricing-plan .tab-panel1 .react-tabs__tab-panel{
        justify-content: center;
    }
    .shared-pricing-plan .single-card{
        width: 46%;
        margin:25px 2%;
    }
}

@media(max-width:650px){
    .shared-pricing-plan{
        margin:45px 0;
        .h5{
            font-size: 15px;
        }
        h1{
            font-size: 20px;
            padding:0 7.5%;
            margin:10px 0 20px;
        }
        .react-tabs__tab-list{
            margin:0 auto 35px;
        }
        .react-tabs__tab{
            padding:10px 30px;
            font-size: 14px;
            margin:1px;
        }
    }
    
    .shared-pricing-plan .tab-panel1{
        padding:0 7.5%;
        .bg-img{
            clip-path: polygon(0 0, 100% 20%, 100% 100%, 0 80%);
        }
    }
    .shared-pricing-plan .single-card{
        width: 400px;
        max-width: 100%;
        margin:25px auto;
        border-radius: 10px;
        h5{
            font-size: 25px;
            margin:30px 0 15px;
        }
        h6{
            font-size: 40px;
            margin-bottom: 5px;
            sup{
                font-size:25px;
            }
        }
        .price{
            margin-bottom:25px;
        }
        .price span{
            font-size: 14px;
        }
        .p{
            font-size: 15px;
            margin-bottom: 20px;
        }
        .feature-item{

            font-size: 15px;
            margin:12.5px 0;
            svg{
                font-size: 18px;
                margin-right: 10px;
            }
        }
        .features{
            margin-bottom:80px;
        }
        .last-row{
            a{
                padding:10px 30px;
            }
            .card{
                font-size: 14px;
                padding-top: 10px;
            }
        }
    }
}

.shared-pricing-plan{
    .single-card{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        
        transition: 0.35s ease-in-out;
        &:hover{
            transform: translateY(-25px);
        }
    }
    .single-card:before{
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0.5px;
        background: #427CFF;
        height: 5px;
        -webkit-transition-property: left, right;
        transition-property: left, right;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
        border-radius: 0 0 15px 15px;
    }
    .single-card:hover:before{
        left: 5px;
        right: 5px;
    }
}

@media(max-width:767px){
    .shared-pricing-plan{
        .single-card{
            &:hover{
                transform: translateY(-15px);
            }
        }
    }
}