.shared-never-lose-password{
    padding:0 10%;
    margin:5% 0;
   .col1{
        width: 35%;
    }
    .other-elements{
        width: 50%;
    }
    .created-img{
        width: 100%;
        position: relative;
    }
    .bg-img{
        content: url('../../../assets/Home/never-lose-password.png');
        width: 60%;
        border-radius: 15px;
        margin-left: auto;
    }
    form{
        border: 1px solid #eee;
        background-color: #fff;
        position: absolute;
        top:10%;
        width: 80%;
        left: 0%;
        border-radius: 15px;
        padding:20px;
    }
    .header{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 20px;
    }
    .circles{
        display: flex;
    }
    .circle{
        width: 15px;
        height: 15px;
        border-radius: 50%;
        margin:0 5px;
        background-color: #ddd;
    }
    .logo-container{
        content: url('../../../assets/Navbar/blue-logo.png');
        width: 40px;
    }
    h6{
        font-size: 20px;
        text-align: center;
    }
    .input-content{
        display: flex;
        align-items: center;
        border:1px solid #ccc;
        width: 90%;
        margin:20px auto;
        padding:0 15px;
        border-radius: 20px;
        &:focus-within{
            border:1px solid #427CFF;
            svg{
                color: #427CFF;
            }
        }
        input{
            height: 42.5px;
            flex: 1;
            border:0;
            font-size: 17px;
            color:#aaa;
            &:focus{
                outline: none;
            }
            &::placeholder{
                color:#aaa;
            }
        }
        svg{
            color:#aaa;
            font-size: 20px;
            margin-bottom: -2px;
            margin-right: 15px;
        }
    }
    button{
        width: 90%;
        margin:20px 5%;
        height: 42.5px;
        background-color: #427CFF;
        color:#fff;
        font-size: 17px;
        border: 0;
        border-radius: 20px;
        cursor: pointer;
    }
}

@media(max-width:1401px){
    .shared-never-lose-password{
       .col1{
            width: 40%;
        }
        .bg-img{
            border-radius: 10px;
        }
        form{
            top:7.5%;
            border-radius: 10px;
            padding:15px;
        }
        .header{
            margin-bottom: 15px;
        }
        .circle{
            width: 13px;
            height: 13px;
        }
        .logo-container{
            width: 35px;
        }
        h6{
            font-size: 17.5px;
        }
        .input-content{
            margin:15px auto;
            padding:0 12px;
            border-radius: 18px;
            input{
                height: 37.5px;
                font-size: 14px;
            }
            svg{
                font-size: 17.5px;
                margin-right: 12px;
            }
        }
        button{
            margin:15px 5%;
            height: 37.5px;
            font-size: 14px;
            border-radius: 18px;
        }
    }
}

@media(max-width:991px){
    .shared-never-lose-password{
        padding:0 5%;
        form{
            width: 90%;
            top:-5%;
        }
        .input-content, button{
            width: 100%;
            margin:15px 0;
        }
    }
}

@media(max-width:767px){
    .shared-never-lose-password{
        padding:0;
        margin:40px 0;
       .col1{
            width: 400px;
            max-width: 85%;
            margin:20px auto;
        }
        .other-elements{
            width: 500px;
            max-width: 100%;
            margin:20px auto;
            padding-left: 5%;
        }
        .bg-img{
            width: 85%;
            border-radius: 10px 0 0 10px;
        }
        form{
            top:5%;
            width: 85%;
        }
        h6{
            font-size: 16px;
        }
        .input-content{
            width: 100%;
            margin:15px 0;
            input{
                height: 35px;
                font-size: 13px;
            }
            svg{
                font-size: 16px;
                margin-bottom: -1px;
                margin-right: 10px;
            }
        }
        button{
            width: 100%;
            margin:15px 0;
            height: 35px;
        }
    }    
}

@media(max-width:345px){
    .shared-never-lose-password{
        .bg-img{
            width: 90%;
        }
        form{
            top:5%;
            width: 90%;
        }
        .input-content{
            input{
                font-size: 12px;
            }
            svg{
                font-size: 15px;
                margin-right: 7.5px;
            }
        }
    }    
}