.shared-keep-safe{
    padding:0 10%;
    margin:5% 0;
   .col1{
        width: 37.5%;
    }
    .other-elements{
        width: 52.5%;
    }
    .created-img{
        width: 100%;
        position: relative;
    }
    .bg-img{
        content: url('../../../assets/Home/keep-safe.png');
        width: 60%;
        border-radius: 15px;
    }
    .inner-created-img{
        border: 1px solid #eee;
        background-color: #fff;
        position: absolute;
        top:10%;
        width: 80%;
        right: 0%;
        border-radius: 15px;
        padding:20px;
    }
    .header{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 20px;
    }
    .circles{
        display: flex;
    }
    .circle{
        width: 15px;
        height: 15px;
        border-radius: 50%;
        margin:0 5px;
        background-color: #ddd;
    }
    button{
        background-color: #427CFF;
        color:#fff;
        font-size: 17px;
        padding:13px 25px;
        border-radius: 25px;
        border:0;
        cursor: pointer;
        display: flex;
        align-items: center;
        width: fit-content;
        svg{
            font-size: 25px;
            margin-right: 10px;
        }
    }
    .mini-item{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin:15px 0;
        padding:10px 7.5px;
        border:1px solid #eee;
        border-radius: 5px;
        transition: 0.2s ease-in-out;
        &:hover{
            background-color: #427CFF;
            border: 1px solid #427CFF;
            .email p, svg{
                color:white;
            }
            .mini-circle{
                background-color: #fff;
            }
        }
    }
    .email, .lock-container{
        display: flex;
        align-items: center;
        margin:0 7.5px;
    }
    .rectangle{
        width: 16px;
        height: 25px;
        border:1px solid #ddd;
        box-shadow: 3px -3px 0px 0px rgba(0,0,0,0.2);
        margin-right: 12px;
    }
    .email p{
        font-size: 15px;
        color:#aaa;
    }
    .mini-circle{
        width: 9px;
        height: 9px;
        border-radius: 50%;
        margin:0 3px;
        background-color: #ddd;
    }
    .lock-container svg{
        font-size: 23px;
        margin-right: 5px;
        color:#ccc;
    }
}

@media(max-width:1401px){
    .shared-keep-safe{
        padding:0 7.5%;
        .bg-img{
            border-radius: 10px;
        }
        .inner-created-img{
            top:10%;
            width: 80%;
            border-radius: 10px;
            padding:15x;
        }
        .header{
            margin-bottom: 15px;
        }
        .circle{
            width: 12.5px;
            height: 12.5px;
        }
        button{
            font-size: 14px;
            padding:11px 20px;
            border-radius: 20px;
            svg{
                font-size: 20px;
                margin-right: 7.5px;
            }
        }
        .mini-item{
            margin:12px 0;
            padding:8px 6px;
        }
        .email, .lock-container{
            margin:0 6px;
        }
        .rectangle{
            width: 13px;
            height: 19px;
            box-shadow: 2px -2px 0px 0px rgba(0,0,0,0.2);
            margin-right: 10px;
        }
        .email p{
            font-size: 12px;
        }
        .mini-circle{
            width: 7px;
            height: 7px;
            margin:0 2.5px;
        }
        .lock-container svg{
            font-size: 20px;
            margin-right: 4px;
        }
    }
    
}

@media(max-width:1040px){
    .shared-keep-safe .left-right{
        display: block;
        .col1{
            width: 300px;
            max-width: 85%;
            margin:20px auto;
        }
    }
    .shared-keep-safe .other-elements{
        width: 100%;
    }
    .shared-keep-safe .created-img{
        width: 500px;
        max-width: 100%;
        margin:20px auto;
    }
}

@media(max-width:570px){
    .shared-keep-safe{
        padding:0;
        margin:35px 0;
        .bg-img{
            width: 85%;
            border-radius: 0 10px 10px 0;
        }
        .inner-created-img{
            top:5%;
            width: 91%;
            right: 2%;
        }
        .circle{
            width: 12px;
            height: 12px;
        }
        button{
            font-size: 13px;
            padding:9px 17px;
            border-radius: 18px;
            svg{
                font-size: 18px;
                margin-right: 7px;
            }
        }
        .mini-item{
            flex-wrap: wrap;
            margin:10px 0;
            padding:3px 5px;
        }
        .email, .lock-container{
            margin:5px;
        }
        .rectangle{
            width: 10px;
            height: 14px;
            margin-right: 7px;
        }
        .email p{
            font-size: 11px;
        }
        .mini-circle{
            width: 6px;
            height: 6px;
            margin:0 2px;
        }
        .lock-container svg{
            font-size: 16px;
            margin-right: 3px;
        }
    }
}

@media(max-width:332px){
    .shared-keep-safe{
        margin:35px 0 100px;
    }
}