.shared-security-trust{
    margin:5% 0 7.5%;
    padding:0 15%;
    h5{
        font-size: 18px;
        color:#427CFF;
        font-weight: normal;
        text-align: center;
    }
    h1{
        font-size: 35px;
        text-align: center;
        margin:15px 0 60px;
    }
    .items1{
        display: flex;
        justify-content: space-between;
    }
    .item1{
        width: 27%;
        transition: 0.35s ease-in-out;
    }
    .icon{
        background: #dfe9ff;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100px;
        height: 100px;
        border-radius: 50%;
    }
    .inner-icon{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 70px;
        height: 70px;
        border-radius: 72% 28% 36% 64% / 65% 72% 28% 35%;
        background-color: #fff;
        font-size: 40px;
        color:#427CFF;
    }
    h6{
        font-size: 19px;
        margin:15px 0;
    }
    p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .shared-security-trust{
        h5{
            font-size: 15px;
        }
        h1{
            font-size: 28px;
            margin:15px 0 50px;
        }
        .icon{
            width: 80px;
            height: 80px;
        }
        .inner-icon{
            width: 50px;
            height: 50px;
            font-size: 30px;
        }
        h6{
            font-size: 16px;
            margin:15px 0;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:1030px){
    .shared-security-trust{
        padding:0 7.5%;
    }
}

@media(max-width:767px){
    .shared-security-trust{
        margin:40px 0;
        h1{
            font-size: 20px;
            margin:15px 0 20px;
        }
        .items1{
            flex-wrap: wrap;
            justify-content: center;
        }
        .item1{
            width: 330px;
            max-width: 100%;
            margin:15px auto;
            text-align: center;
        }
        .icon{
            width: 75px;
            height: 75px;
            margin:0 auto;
        }
        .inner-icon{
            width: 50px;
            height: 50px;
            font-size: 25px;
        }
        h6{
            font-size: 15px;
            margin:15px 0;
        }
        p{
            font-size: 14px;
        }
    }
}

.shared-security-trust .item1:hover{
    .inner-icon{
        transform: scale(1.2);
        transition: 0.3s ease-in-out;
    }

}