.shared-fingerprint-password{
    background: linear-gradient(rgba(29, 42, 78, 0.6), rgba(29, 42, 78, 0.6)), url('../../../assets/Home/fingerprint-password.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    padding:10% 65% 10% 10%;
    margin:5% 0;
    color:#fff;
    h1{
        font-size: 40px;
    }
    p{
        font-size: 18px;
        margin:20px 0;
    }
    a{
        color:#fff;
        display: flex;
        align-items: center;
        font-size: 18px;     
    }
    a svg{
        font-size: 40px;
        margin-right:10px;
    }
}

@media(max-width:1600px){
    .shared-fingerprint-password{
        padding:10% 55% 10% 10%;
    }
}

@media(max-width:1401px){
    .shared-fingerprint-password{
        padding:10% 58.5% 10% 10%;
        h1{
            font-size: 35px;
        }
        p{
            font-size: 15px;
            margin:20px 0;
        }
        a{
            font-size: 15px;     
        }
        a svg{
            font-size: 35px;
            margin-right:7.5px;
        }
    }
}

@media(max-width:1401px){
    .shared-fingerprint-password{
        padding:10% 47.5% 10% 10%;
    }
}

@media(max-width:767px){
    .shared-fingerprint-password{
        padding:70px 7.5%;
        h1{
            font-size: 25px;
        }
        p{
            font-size: 14px;
        }
        a{
            font-size: 14px;     
        }
        a svg{
            font-size: 30px;
        }
    }
}